import './App.css';
import { useEffect } from 'react';
import React, { useState } from "react";
import logo from "../src/logo.svg"
import lowVol from "../src/lowVol.svg";
import highVol from "../src/highVol.svg";
import powerOff from "../src/powerOff.svg";
import powerOn from "../src/powerOn.svg"
import f11 from "../src/F11.svg";
import defaultMusic from "../src/defaultMusic.svg"


function App() {
  const [volume, setVolume] = useState(0)
  const [titlee, displayTitle] = useState("")
  const [pwrbtn, setPwrbtn] = useState(false)
  const [audible, setAudible]=useState(false)





  function getCurrentTabFavIconUrl(callback) {
    var queryInfo = {
      active: true,
      currentWindow: true
    };
    window.chrome.tabs.query(queryInfo, function (tabs) {
      callback(tabs[0].favIconUrl);
    });
  }

  document.addEventListener('DOMContentLoaded', function () {
    getCurrentTabFavIconUrl(function (url) {
      if (url) {
        populateIcon(url);
      }
    });
  });
  function populateIcon(url) {

    var iconElement = document.getElementById('pic');
    iconElement.src = url;
    var urlElement
    // urlElement.innerHTML = "<a href=\"" + url + "\" target=\"_blank\">" + url + "</a>";
  }
  function getFileName(path) {
    return path.split('/').pop();
  }

  useEffect(() => {
    window.chrome.storage.local.get(null, function (e) {

      let toggleStat = document.getElementById("power")

      toggleStat.checked = e.toggleStatus;


      window.chrome.tabs.query({ active: true, windowId: window.id },
        function (tabs) {
          var currentUrl = tabs[0].url
          setAudible(tabs[0].audible)
          window.chrome.storage.local.get("myData", function (val) {

            let b = val.myData
            var flag = false
            let tempVol = 0.5;
            for (var i = 0; i < b.length; i++) {

              if (currentUrl === b[i].url) {
                flag = true
                tempVol = b[i].vol
              }
            }
            // console.log(flag, "flag thing")

            setVolume(tempVol)
            sliderGradient(tempVol)
            if (e.toggleStatus === true) {
              window.chrome.runtime.sendMessage({
                msg: "Slidder_Triggered",
                e: tempVol
              });
            }
          })

        })

      window.chrome.tabs.query({ active: true, windowId: window.id },
        function (tabs) {
          // console.log(tabs)
          displayTitle(tabs[0].title)

        }
      )
    });
  }, [])

  const volumeChanged = (e) => {
    window.chrome.tabs.query({ active: true, windowId: window.id },
      function (tabs) {
        var Url = tabs[0].url


        window.chrome.storage.local.get("myData", function (val) {

          let a = val.myData
          let f = false
          let newObj = { url: Url, vol: e }
          for (var i = 0; i < a.length; i++) {
            if (Url == a[i].url) {
              f = true
              a[i].vol = e

            }
          }

          if (!f) {
            a.push(newObj)
          }
          window.chrome.storage.local.set({ "myData": a })
          window.chrome.storage.local.get("toggleStatus", function (val) {
            if (val.toggleStatus) {
              window.chrome.runtime.sendMessage({
                msg: "Slidder_Triggered",
                e: e
              });
            }
            else {
              document.getElementById("power").click()
            }
          })
        })

      });
  }

  const turnOff = (e) => {
    window.chrome.runtime.sendMessage({
      msg: "turnOff"
    });
    window.close()
  }


  function sliderGradient(val) {
    var slider1 = document.getElementById('myRange');

    var perc = Number(val) * 25
    var color = 'linear-gradient(90deg, rgb(112, 164, 230)' + perc + '%, rgb(255, 235, 235)' + perc + '%)';
    slider1.style.background = color;

  }  

  function powerBtn() {
    setPwrbtn(!pwrbtn)
    
    if (!pwrbtn) {
      setVolume(0.1)
      
      sliderGradient(0.1)
      turnOff()
      document.getElementsByClassName("disc-rotate").style.animation="none"
      // window.close()
     }
  }

  function decreaseSound(){
    console.log("hello decrease")
    if(volume>0.1){
      setVolume(volume-0.1)
      sliderGradient(volume-0.1)
    }

  }

  function increaseSound(){
    console.log("hello increase")
   if(volume<4.0){
    setVolume(volume+0.1)
    sliderGradient(volume+0.1)
   }

  }




  return (
    <div className="App">


      <header className="App-header">
      </header>

      <div className='logo'><img src={logo} /> </div>


      <div id='power'>{!pwrbtn ?
        <img id='pwrImg' onClick={powerBtn} src={powerOn} /> :
        <img id='pwrImg' onClick={powerBtn} src={powerOff}  />
      }</div>

      <div className='container'>
        <div id='soundValue'>
          <h3 > {Math.round(volume * 100)}{"% Boosted"}</h3>
        </div>

        <div id='rangePanel'>
          <span id='lowSound' onClick={decreaseSound}>
            <button className='button' ><img src={lowVol} /></button>
            <p className="minVal">10%</p>
          </span>
          <input type="range"
            id='myRange'
            min={0.1}
            max={4}
            step={0.1}
            value={volume}
            disabled={pwrbtn}
            onChange={e => {
              setVolume(e.target.valueAsNumber);
              console.log(e.target.valueAsNumber)
              volumeChanged(e.target.valueAsNumber);
              sliderGradient(e.target.valueAsNumber)

            }}
          />
          <span id='highSound' onClick={increaseSound}>
            <button className='button'><img src={highVol} /></button>
            <p className="maxVal">400%</p>
          </span>
        </div>
        <div id='infoSec'>
          <div id='audioPic'>
            <img id='pic' src={defaultMusic} />
          </div>
          <div id='disc' className={audible &&'disc-rotate'}></div>

          <div className="title">{!pwrbtn? audible? titlee:" No audio is being played in this tab." : "Turn on Sound booster for better Experience"}</div>
          <div className='defaultTitle'></div>
        </div>


      </div>
       <div id='footer'>
         Press <img src={f11} /> Keys to experience videos in full screen
       </div>
      <span id='feedback'><a href="https://chrome.google.com/webstore/detail/sound-booster/jikgjlbgdocjaglnihmgifcipikbghmb/reviews" target='_blank' >rate us</a></span>

    </div>
  );

}

export default App;
